/* =================================================================
MISC STYLING
================================================================= */
@import url("https://fonts.googleapis.com/css2?family=Noto+Sans+Mono:wght@400;700&display=swap");
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

html {
  overflow-x: hidden;
}

body {
  margin: 0 px;
  font-family: "Noto Sans Mono", monospace;
  position: relative;
  /* font-size: 1.3125rem; */
}

.w-100 {
  width: 100% !important;
}

.section {
  padding: 0px 6rem;

  @media only screen and (max-width: 575px) {
    padding: 15px !important;
  }
}

section.navigation .container {
  @media only screen and (max-width: 991px) {
    max-width: 100%;
  }
}

.inner-section {
  padding: 0 6rem;

  @media only screen and (max-width: 575px) {
    padding: 15px !important;
  }
}

@media (min-width: 240px) and (max-width: 575px) {
  .inner-section.story-section {
    padding: 70px 35px 40px !important;
    justify-content: center;
  }
}

.section-heading {
  font-size: 34px;
  color: #616770;
  font-weight: bold;
  text-transform: uppercase;

  @media only screen and (max-width: 575px) {
    font-size: 22px;
    margin-bottom: 30px !important;
    padding: 0px !important;
    padding-left: 0px !important;
  }
}

.heading-styling {
  color: black;
  margin-bottom: 3rem !important;
  margin-top: 0 !important;
}

.container {
  max-width: 1320px;
  width: 100%;
  padding: 0 15px;
  margin: 0 auto;
  position: relative;

  @media (min-width: 476px) and (max-width: 767px) {
    max-width: 540px;
  }

  @media (min-width: 768px) and (max-width: 991px) {
    max-width: 720px;
  }

  @media only screen and (min-width: 992px) { 
    position: relative;
  }

  @media (min-width: 992px) and (max-width: 1199px) { 
    max-width: 960px;
  }

  @media (min-width: 1200px) and (max-width: 1399px) {
    max-width: 1140px;
  }

  @media (min-width: 1400px) {
    max-width: 1320px;
    width: 100%;
    padding: 0 15px;
    margin: 0 auto;
  }
}

p {

  @media only screen and (max-width: 575px) {
    font-size: 14px;
    line-height: 1.8;
  }
}

ul {

  @media (min-width: 240px) and (max-width: 575px) {
    font-size: 15px;
  }

  @media only screen and (max-width: 575px) {
    font-size: 12px;
    font-weight: 100;
  }
}

.row {
  /* --bs-gutter-x: 1.5rem;
  --bs-gutter-y: 0; */
  display: flex;
  flex-wrap: wrap;
  margin-top: calc(var(--bs-gutter-y) * -1);
  margin-right: calc(var(--bs-gutter-x) * -0.5);
  margin-left: calc(var(--bs-gutter-x) * -0.5);

  > * {
    flex-shrink: 0;
    /* width: 100%; */
    max-width: 100%;
    padding-right: calc(var(--bs-gutter-x) * 0.5);
    padding-left: calc(var(--bs-gutter-x) * 0.5);
    margin-top: var(--bs-gutter-y);
  } 
}

article {

  @media screen and (min-width: 992px) { 
    max-width: 1000px;
    margin: 0 auto;
    padding: 10px;
  }
}
.mb-0 {
  margin-bottom: 0 !important;
}

.my-5 {
  margin-top: 3rem !important;
  margin-bottom: 3rem !important;
}

.m-0 {
  margin: 0 !important;
}

.black {
  color: #000000 !important;
}

/*delete if not needed after text*/
.mb-5 {
  margin-bottom: 3rem !important;
}


/* =================================================================
NAVIGATION 
================================================================= */
header {
  z-index: -10;
  width: 100%;
}
/* possibly dont need
.navbar {
  width: 100%;
  /* height: 70px !important; /
}

.nav-link {
  color: black !important;
  text-transform: uppercase;
} */

.navbar-brand {
  font-weight: 700;
  text-transform: uppercase;

  @media only screen and (max-width: 575px) {
    font-size: 14px;
  }
}

nav {

  display: flex;
  justify-content: flex-end;
  align-items: center;

  @media (min-width: 576px) and (max-width: 767px) {
    padding: 0 15px;
  }

  @media (min-width: 768px) and (max-width: 991px) {
    padding: 0 15px !important;
  }
  @media only screen and (max-width: 991px) {
    justify-content: flex-start;
  }

  ul {
    list-style: none;
    margin: 0;
    padding: 0;

    @media only screen and (max-width: 991px) {
      display: none;
    }

    li {
      float: left;
      position: relative;

      @media only screen and (max-width: 991px) {
        float: none;
      }

      a {

        @media only screen and (max-width: 991px) {
          padding: 17px 0;
          line-height: 20px;
        }
      }
    }
  }
}
.learn-more-btn {
  border: 1px solid #000;
  /* padding: 8px 0; */
  margin-left: 20px;
  display: inline-block;

  @media only screen and (max-width: 991px) {
    margin-left: 0 !important;
    margin-top: 20px;
  }

  @media (min-width: 992px) and (max-width: 1199px) {
    margin-left: 10px !important;
  }

  a {
    padding: 8px 20px;
  }

  a:hover, a:focus {
    font-weight: normal;
    /* color: #00f0ff; */
    background-color: #000;
    color: #fff;
  }
}



nav ul li a, nav ul li a:visited {
  display: block;
  padding: 0 20px;
  line-height: 20px;
  color: #000;
  text-decoration: none;
  text-transform: uppercase;
  transition: 0.5s;
  -webkit-transition: 0.5s;
  -moz-transition: 0.5s;
  -ms-transition: 0.5s;
  -o-transition: 0.5s;
  @media (min-width: 992px) and (max-width: 1199px) {
    padding: 8 13px !important;
  }

}
nav ul li a:hover, nav ul li a:visited:hover, nav ul li a:focus, nav ul li a.active {
  font-weight: bold;
}
nav ul li a:not(:only-child):after, nav ul li a:visited:not(:only-child):after {
  padding-left: 4px;
  border: none;
  font-family: "Font Awesome 5 Pro";
  content: "\f078";
  font-size: 12px;
  color: #5d0eb6;
  font-weight: bold;
}

@media (max-width: 991px) {
  nav ul li a {
    padding: 17px 0;
    line-height: 20px;
  }
}

.nav-mobile {

  display: none;
  position: absolute;
  top: 50px;
  right: 30px;
  background: transparent;
  height: 36px;
  width: 36px;
  border: 2px solid #000;

  @media (min-width: 240px) and (max-width: 575px) {
    top: 28px !important;
    right: 15px !important;
  }

  @media (min-width: 576px) and (max-width: 767px) {
    top: 35px !important;
  }

  @media only screen and (max-width: 991px) {
    display: block;
  }
}

.nav-dropdown {
  position: absolute;
  display: none;
  z-index: 1;
  background-color: #fff;
 left: 50%;
  top: 59px;
  padding: 15px 0;
  border: 3px solid #05f0ff;
  transform: translate(-50%, 0);
  -webkit-transform: translate(-50%, 0);
  -moz-transform: translate(-50%, 0);
  -ms-transform: translate(-50%, 0);
  -o-transform: translate(-50%, 0);

  @media only screen and (max-width: 991px) {
    /*position: static;*/
    border: none;
    position: relative !important;
    top: 0 !important;
    left: 0 !important;
    padding: 0 !important;
    transform: translate(0, 0);
    -webkit-transform: translate(0, 0);
    -moz-transform: translate(0, 0);
    -ms-transform: translate(0, 0);
    -o-transform: translate(0, 0);
  }
}

nav ul li ul li {
  min-width: 285px;
  text-align: center;

  @media only screen and (max-width: 991px) {
    text-align: left;
  }
}

nav ul li ul li a {
  padding: 13px;
  line-height: 20px;
  text-transform: capitalize;

  @media only screen and (max-width: 991px) {
    padding-left: 20px;
    color: #94989e !important;
  }

  @media (min-width: 992px) and (max-width: 1199px) {
    padding: 13px !important;
  }
}

.brand {
  position: absolute;
  line-height: 20px;
  text-transform: uppercase;
  font-size: 1.4em;
  left: 0;
  top: 0px;

  @media (min-width: 240px) and (max-width: 575px) {
    padding-left: 0 !important;
  }

  @media only screen and (max-width: 991px) {
    position: relative;
    padding-left: 15px;
  }

  a, a:visited {
    color: #000;
    text-decoration: none;
    position: relative;
    left: 120px;
    top: 10px;

    @media only screen and (max-width: 991px) {
      top: 0;
      left: 0;
    }
  }
}

img.brand-logo {
  position: absolute;
  left: 0;
  top: -6px;

  @media (min-width: 576px) and (max-width: 767px) {
    width: 70px;
    height: 70px;
  }

  @media only screen and (max-width: 991px) {
    position: relative;
    left: 0;
    top: 0;
  }
}



.navigation {
  padding: 32px 0 30px;

  @media (min-width: 240px) and (max-width: 575px) {
    position: relative;
  }

  @media only screen and (max-width: 991px) {
    padding: 20px 0 18px;
  }
}

.inner-triangle {

  @media only screen and (max-width: 991px) {
    display: none;
  }

  @media screen and (min-width: 992px) { 
    position: absolute;
    bottom: -19px;
    left: -15px;
    width: 0;
    height: 0;
    border-left: 15px solid transparent;
    border-right: 15px solid transparent;
    border-bottom: 15px solid #fff;
  }
}

.triangle-left {

  @media only screen and (max-width: 991px) {
    display: none;
  }

  @media screen and (min-width: 992px) { 
    width: 0;
    height: 0;
    border-left: 17px solid transparent;
    border-right: 17px solid transparent;
    border-bottom: 17px solid #05f0ff;
    position: absolute;
    bottom: 100%;
    left: 50%;
    transform: translate(-50%, 0);
    -webkit-transform: translate(-50%, 0);
    -moz-transform: translate(-50%, 0);
    -ms-transform: translate(-50%, 0);
    -o-transform: translate(-50%, 0);
  }
}

.nav-list {

  @media screen and (min-width: 992px) { 
    /* display: block !important; */
    display: flex !important;
    justify-content: center;
    align-items: center;
    position: relative;
 }
}

#nav-toggle {

  position: relative;
  left: 0;
  top: 0;
  cursor: pointer;
  height: 100%;
  display: block;
 
 span {
  position: relative;
  top: 15px;
  left: 4px;
  }

}


#nav-toggle span, #nav-toggle span:before, #nav-toggle span:after {
  cursor: pointer;
  border-radius: 1px;
  height: 3px;
  width: 24px;
  background: #000;
  position: absolute;
  display: block;
  content: '';
  transition: all 300ms ease-in-out;
}

#nav-toggle span:before {
  top: -10px;
}
  
#nav-toggle span:after {
  bottom: -10px;
}



