/* =================================================================
ACCORDIAN SECTION 
================================================================= */
.accordion {
  background-color: transparent;
  cursor: pointer;
  width: 100%;
  transition: 0.4s;
  font-size: 18px;
  font-weight: bold;
  color: #616770;
  padding-bottom: 10px;
  text-align: left;
  border: none;
  padding: 20px 0;

  @media (min-width: 240px) and (max-width: 575px) {
    padding-bottom: 20px;
  }

  @media only screen and (max-width: 575px) {
    font-size: 14px;
    padding-bottom: 10px;
    text-align: left;
  }

  span {
    color: #5d0eb6;
    margin-right: 70px;

    @media (min-width: 240px) and (max-width: 575px) {
      margin-right: 20px;
    }

    @media (min-width: 576px) and (max-width: 767px) {
      margin-right: 45px;
    }
  }
}

.accordion.active{
  border-bottom: none;
}
.accordion:after {
  content: "\f067";
  color: #5d0eb6;
  /* float: right; */
  margin-left: 5px;
  font-size: 26px;
  padding-right: 20px;
  font-family: 'Font Awesome 5 Pro';
  font-weight: 300;
  position: absolute;
  right: 0;

  @media (min-width: 240px) and (max-width: 575px) {
    font-size: 20px;
    padding-right: 0;
  }
}

button.accordion {
  display: flex;
  align-items: center;
  /* justify-content: space-evenly; */
  position: relative;
}

.accordion.active:after{
  content: "\f068";
}

